/* =================MAIN HOME PAGE=============== */
.HomePage {
    box-sizing: border-box;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}
.HomePage > * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-y: hidden;
    width: 100%;

}
.HomePage-inner {
    background-color: #064976;
    height: 100vh;
    width: 85vw;
    margin-top: 2.8vw;
    border-radius: 2vw;
    display: flex;
}

.HomePage-inner-left {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: white;

}

.HomePage-inner-left p {
    margin-top: 2vw;
}

.HomePage-inner-left>* {
    width: 40vw;
}

.HomePage-inner-left>button {
    border: none;
    background-color: #1992E0;
    padding: .5vw 2vw;
    color: white;
    margin-top: 3vw;
    border-radius: 30px;
    width: 20vw;
}

.HomePage-inner-right {
    width: 50%;
}

.HomePage-inner-right img {
    width: 42.5vw;
    height: 100vh;
    border-radius: 0 2vw 2vw 0;
}

.self-sovereign {
    box-sizing: border-box;
    overflow-y: hidden;
    background-color: #e2e7eb;
    width: 98.75vw;
    /* width: 100vw; */
    margin-top: 2.8vw;
    display: flex;

}

.self-sovereign-left {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.self-sovereign-left img {
    width: 40vw;
    height: 40vw;
}

.self-sovereign-right {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: start;
}

.self-sovereign-right * {
    width: 40vw;
}

.verifiable-credentials {
    height: 70vh;
    width: 98.75vw;
    box-sizing: border-box;
    display: flex;
}

.verifiable-credentials-left {
    width: 50vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.verifiable-credentials-left>* {
    width: 45vw;
}

.verifiable-credentials-right {
    width: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.verifiable-credentials-right img {
    width: 35vw;
    height: 25vw;
    border-radius: 20px;
}

.verification {
    width: 98.75vw;
    overflow-y: hidden;
    box-sizing: border-box;
    height: 90vh;
    background-color: #e2e7eb;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4vw;
}

.verification-left {
    width: 45vw;
}

.verification-left>* {
    width: 45vw;
}

.verification-right {
    width: 40vw;
}

.verification-right img {
    width: 40vw;
    height: 30vw;
    border-radius: 10px;
}

.did {
    width: 98.75vw;
    overflow-y: hidden;
    box-sizing: border-box;
    display: flex;
    margin-top: 3vw;
    height: 70vh;
}

.did-left {
    width: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.did-left img {
    width: 40vw;
    height: 25vw;
    border-radius: 10px;
}

.did-right {
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.did-right>* {
    width: 40vw;
}

/* =================REGISTER PAGE==================== */
.gotDid {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 70vw;
    height: 20vw;
    background-color: #1992E0;
    position: absolute;
    top: 33%;
    left: 15%;
}

.gotDid button {
    border: 2px solid white;
    background-color: #1992E0;
    padding: .5vw 2vw;
    color: white;
    margin-top: 10px;
}

.RegisteredModal {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.RegisteredModal-inner {
    width: 60vw;
    background-color: #064976;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 70%;
    border-radius: 0vw 2vw 0vw 2vw;
}

.registeredModal-Desc {
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.RegisteredModal .btnGroup {
    margin-bottom: 3vw;
    margin-top: 1vw;
    width: 100%;
    text-align: right;
}

.RegisteredModal .btnGroup button {
    border: 1px solid white;
    border-radius: 7px 0 7px 0;
    background-color: #064976;
    color: white;
    padding: .7vw 4vw;
    min-width: 10vw;
    margin-right: 2vw;
}

/* ===========LOGIN PAGE========= */
.Login {
    height: 100%;
}

.Login form input,
.Register form input {
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px 20px;
    width: 25vw;
    margin-top: 10px;
}

/* =================LOGGED IN DASHBOARD=============== */
.loggedInHomePage {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loggedInHomePage h2,
.createNewCredential h2,
.userProfile h2,
.Credentials h2,
.ProviderProfile h2,
.certificate h2,
.IssuedCredentials h2,
.CredentialAccess h2 {
    background-color: #064976;
    text-align: center;
    border-radius: 0 0 5px 5px;
    color: white;
    height: 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loggedInHomePage .boxesGroup {
    margin-top: 5vw;
    display: flex;
}

.boxesGroup div {
    border: 1px solid rgb(219, 212, 212);
    padding: 2vw;
    cursor: pointer;
}

.loggedInHomePage .btnGroup {
    /* margin-top: 5vw; */
    width: 70%;
    text-align: end;
}

.loggedInHomePage .btnGroup button {
    background-color: #0f8bde;
    border: 1px solid gray;
    /* border-radius: 5px; */
    color: white;
    padding: .5vw 2vw;
    margin: 3vw 2.5vw 0 0vw;
}

/* =================LOGGED IN CREATE NEW CREDENTIAL=============== */
.createNewCredential {
    box-sizing: border-box;
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.createNewCredential form {
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
    width: 40vw;
    height: 25vw;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    justify-self: center;
    margin-top: 5vw;
}

.createNewCredential button {
    align-self: flex;
    padding: .5vw 1vw;
    border-radius: 5px 0px 5px 0px;
    background-color: #0f8bde;
    margin: 1vw;
    border: 1px solid gray;
    color: white;
}

.PromptModal {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.PromptModal-inner {
    background-color: #064976;
    color: white;
    width: 50vw;
    height: 30vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px 0 10px 0;
}

.PromptModal-inner button {
    margin-top: 1vw;
    padding: .5vw 2vw;
    background-color: #0fa7de;
    border: 1px white solid;
    border-radius: 0 0px 0 0px;
    color: white;
}

/* =================USER PROFILE=============== */
.userProfile {
    width: 100%;
    /* background-color: #0f8bde; */
    /* height: 90%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.userProfile-btnGroup {
    margin-top: 4vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    right: -24%;
}

.userProfile-btnGroup button {
    background-color: #0fa7de;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
    color: white;
    border-radius: 1px;
    margin-left: 20px;
    padding: .5vw 2vw;
    border: none;
    top: 10%;
}

.userProfile table {
    margin-top: 3vw;
    width: 80vw;
}

.ProviderProfile {
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ProviderProfile table {
    margin-top: 3vw;
    width: 80vw;
}

.ProviderProfile h3 {
    width: 40vw;
    background-color: #1992E0;
    border-radius: 10px;
    margin-left: 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tdProviderAddress {
    cursor: pointer;
}

.tdProviderAddress span {
    visibility: hidden;
    width: 60px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px; 
    font-size: 10px;
    /* Position the tooltip text - see examples below! */
    position: absolute;
    margin-right: 5vw;
    z-index: 1;
}

.tdProviderAddress:hover span {
    visibility: visible;
}

.ProviderProfile table button {
    margin-top: 10px;
}

